import Vue from 'vue';
import App from './App.vue';
import './registerServiceWorker';
import router from './router';
import store from './store';
import Nav from '@/components/Nav.vue';
import Layout from '@/components/Layout.vue';
import '@/assets/js/icon';
import Icon from '@/components/Icon.vue';

Vue.config.productionTip = false;
Vue.component('Nav', Nav);
Vue.component('Layout', Layout);
Vue.component('Icon', Icon);

const initData = [{id:'1', name:'衣'},{id:'2', name:'食'},{id:'3', name:'住'},{id:'4', name:'行'}]
const _idMax = 4
if(window.localStorage.getItem('tagList') === '[]' || window.localStorage.getItem('tagList') === null){
  window.localStorage.setItem('tagList',JSON.stringify(initData))
  window.localStorage.setItem('_idMax',JSON.stringify(_idMax))
}
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');
